.snake-game {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  
  .grid {
    display: grid;

    grid-template-rows: repeat(20, 20px);
    grid-template-columns: repeat(20, 20px);
    gap: 1px;
    border: 1px solid #444;

  }
  
  .cell {
    width: 20px;
    height: 20px;
    background-color: #949494;
  }
  
  .snake {
    background-color: #000;
  }
  
  .food {
    background-color: red;
  }
  
  .game-over {
    margin-top: 20px;
    font-size: 24px;
    color: red;
  }
  .controls {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  
  .controls button {
    margin: 5px;
    padding: 10px;
    font-size: 16px;
  }